import FontAwesomeIcon from '@fortawesome/react-fontawesome'
import * as React from 'react'
import { Button, Grid, Message, Segment, TextArea } from 'semantic-ui-react'
import BaseField from './BaseField'
import ResponsiveImage from './ResponsiveImage'

interface IProps {
  loading?: boolean
  onPrimaryImageClick?: () => any
}

class ImageDescribe extends React.Component<IConnectProps> {
  public removeImage = (e: React.MouseEvent<HTMLButtonElement>, index: number) => {
    e.preventDefault()
    this.props.removePhoto(index)
  }

  public moveImage = (e: React.MouseEvent<HTMLButtonElement>, a: number, b: number) => {
    e.preventDefault()
    const length = this.props.images.length - 1
    if (a < 0 || b < 0 || a > length || b > length) {
      return
    }
    this.props.movePhoto(a, b)
  }
  public render() {
    const { images, loading, onPrimaryImageClick } = this.props
    return (
      <>
        {images.length ? (
          <Message icon={true}>
            {loading ? (
              <FontAwesomeIcon icon={['fas', 'spinner']} pulse={true}/>
            ) : (
                <FontAwesomeIcon icon={['fas', 'camera-alt']} />
              )}
            <Message.Content>
              <Message.Header>Bilder for annonsen</Message.Header>
              Her kan du legge til en beskrivelse av hvert bilde, endre rekkefølge og slette bilder.
          </Message.Content>
          </Message>) : null}
        {images.map(
          ({ public_id }, i) => {
            const isFrontCard = !i
            return (
              <Segment key={i}>

                <Grid columns="equal" stackable={true}>

                  <Grid.Column width={5}>
                    <ResponsiveImage
                      publicId={public_id}
                      semanticProps={{
                        fluid: true,
                        attached: 'top',
                        ...isFrontCard && onPrimaryImageClick && {
                          onClick: () => onPrimaryImageClick(),
                          style: {
                            cursor: 'pointer',
                          },
                        },
                      }}
                    />
                    {!isFrontCard && (

                      <Button.Group fluid={true} widths="5" attached="bottom" style={{ margin: 0 }}>
                        <Button
                          disabled={i === 1}
                          onClick={(e) => this.moveImage(e, i, i - 1)}
                        >
                        <FontAwesomeIcon icon={['fas', 'arrow-up']} />
                        </Button>
                        <Button
                          disabled={i === images.length - 1}
                          onClick={(e) => this.moveImage(e, i, i + 1)}
                        >
                        <FontAwesomeIcon icon={['fas', 'arrow-down']} />
                        </Button>
                        <Button
                          negative={true}
                          onClick={(e) => this.removeImage(e, i)}
                        >
                        <FontAwesomeIcon icon={['fas', 'trash-alt']} />
                        </Button>
                      </Button.Group>
                    )}
                  </Grid.Column>
                  <Grid.Column>

                    <BaseField
                      rows={5}
                      label={
                        `Beskrivelse av bildet${
                        isFrontCard
                          ? ' (Dette vil være bildet som vises på fremsiden.)'
                          : ''}`}
                      model={`auction.images[${i}].description`}
                      typeComponent={TextArea}
                    />
                  </Grid.Column>
                </Grid>
              </Segment >
            )
          },
        )}

      </>
    )
  }
}

import { connect, Dispatch } from 'react-redux'
import { actions } from 'react-redux-form'
import { IStoreState } from 'store'
const connectCreator = connect(
  ({ auction }: IStoreState) => {
    return {
      images: auction.images,
    }
  },
  (dispatch: Dispatch<{}>) => {
    return {
      movePhoto: (fromIndex: number, toIndex: number) =>
        dispatch(actions.move('auction.images', fromIndex, toIndex)),
      removePhoto: (index: number) =>
        dispatch(actions.remove('auction.images', index)),
    }
  },
)

type IConnectProps = typeof connectCreator.allProps & IProps
export default connectCreator(ImageDescribe)



