import * as React from 'react'
import Script from 'react-load-script'
import {
  Button,
  ButtonProps,
  Loader,
} from 'semantic-ui-react'

interface IProps {
  onChange: (result: ICloudinaryUploadResult[]) => any
  widgetProps?: {
    [s: string]: any,
  }
  buttonProps: ButtonProps
  openUploadWidgetFunc?: (s: () => any) => any
}

interface IState {
  scriptA: boolean
  scriptB: boolean
  scriptC: boolean
  cloudinary: any
}
// Hack cloudinary. WOWSER
(window as any).$ = () => null
require('assets/js/cloudinarywidget.js')

interface ICloudinaryUploadResult {
  public_id: string
  version: number
  signature: string
  width: number
  height: number
  format: string
  resource_type: string
  created_at: string
  tags: string[]
  bytes: number
  type: string
  etag: string
  placeholder: false
  url: string
  secure_url: string
  original_filename: string
  path: string
  thumbnail_url: string
}

const text = {
  'powered_by_cloudinary': 'Powered by Cloudinary - Image management in the cloud',
  'sources.local.title': 'Egne filer',
  'sources.local.drop_file': 'Dra og slipp bildet ditt her',
  'sources.local.drop_files': 'Dra og slipp bildene dine her',
  'sources.local.drop_or': 'Eller',
  'sources.local.select_file': 'Velg bilde',
  'sources.local.select_files': 'Velg bilder',
  'sources.url.title': 'Web adresse',
  'sources.url.note': 'En offentlig web adresse til et bilde:',
  'sources.url.upload': 'Last opp',
  'sources.url.error': 'Vennligst benytt en gyldig adresse',
  'sources.camera.title': 'Kamera',
  'sources.camera.note': 'Vennligst sørg for at din nettleser tillater siden å bruke ditt kamera.',
  'sources.camera.capture': 'Ta bilde',
  'progress.uploading': 'Sender av gårde...',
  'progress.upload_cropped': 'Last opp',
  'progress.processing': 'Klargjør reptilene...',
  'progress.retry_upload': 'Vennligst prøv igjen',
  'progress.use_succeeded': 'OK',
  'progress.failed_note': 'Noe gikk galt under opplastingen',
}

const CLOUD_NAME = process.env.REACT_APP_CLOUDINARY_CLOUD_NAME
const UPLOADPRESET = process.env.REACT_APP_CLOUDINARY_UPLOAD_PRESET

declare global {
  interface Window { cloudinary: any }
}

export default class extends React.Component<IProps, IState> {
  public state: IState = {
    scriptA: false,
    scriptB: false,
    scriptC: false,
    cloudinary: null,
  }
  public openUploadWidget = () => {
    this.state.cloudinary && this.state.cloudinary.open()
  }
  public componentDidMount() {
    if (this.props.openUploadWidgetFunc) {
      this.props.openUploadWidgetFunc(this.openUploadWidget)
    }
  }
  public isAllLoaded = (
    { scriptA, scriptB, scriptC }: {
       scriptA: boolean, scriptB: boolean, scriptC: boolean,
     }) => scriptA && scriptB && scriptC

  public updateScriptStatus = (key: string) => {
    this.setState(({cloudinary, scriptA, scriptB, scriptC }) => {
      const newState = ({cloudinary, scriptA, scriptB, scriptC, [key]: true })
      const allLoaded = this.isAllLoaded(newState)

      if (allLoaded) {
        newState.cloudinary = window.cloudinary.createUploadWidget(
          {
            cloud_name: CLOUD_NAME,
            upload_preset: UPLOADPRESET,
            theme: 'minimal',
            min_image_width: 200,
            cropping_validate_dimensions: true,
            cropping_show_back_button: true,
            cropping_default_selection_ratio: 1,
            resource_type: 'image',
            client_allowed_formats: ['png', 'gif', 'jpeg'],
            button_caption: 'Last opp',
            text,
            sources: [
              'local',
              'url',
              // 'image_search',
              // 'camera',
              // 'dropbox',
              // 'facebook',
              // 'instagram'
            ],
            max_files: 8,
            maxFiles: 2,
            ...this.props.widgetProps,
          },
          (_error: any, result: ICloudinaryUploadResult[]) => {
            if (_error) {
              // tslint:disable-next-line
              console.log(_error)
            }
            if (result) {
              this.props.onChange(result)
            }
          })
      }
      return newState
    })

  }

  public render() {
    const { buttonProps } = this.props
    const allLoaded = this.isAllLoaded(this.state)
    if (!allLoaded) {
      return (
        <>
          <Loader active={true} inline={true} />
          <Script
            attributes={{ id: 'cloudinary-widget', defer: true }}
            url={`//widget.cloudinary.com/n/${CLOUD_NAME}/151/external/all.min.js`}
            onLoad={() => this.updateScriptStatus('scriptA')}
          />
          <Script
            attributes={{ id: 'cloudinary-widget', defer: true }}
            url="//cdnjs.cloudflare.com/ajax/libs/rollbar.js/2.2.7/rollbar.min.js"
            onLoad={() => this.updateScriptStatus('scriptC')}
          />
          <Script
            attributes={{ id: 'cloudinary-widget', defer: true }}
            url={`//widget.cloudinary.com/n/${CLOUD_NAME}/151/index.js`}
            onLoad={() => this.updateScriptStatus('scriptB')}
          />
        </>
      )
    }
    return (
      <Button
        loading={!this.state.cloudinary}
        onClick={(e) => {
          e.preventDefault()
          this.openUploadWidget()
        }}
        {...buttonProps}
      />
    )
  }
}



