import MessageNoAuctionsLeft from 'components/Auction/MessageNoAuctionsLeft'
import NewAdForm from 'components/Forms/NewAdForm'
import DividerHeader from 'components/utils/DividerHeader'
import Meta from 'components/utils/Meta'
import RequiresLogin from 'components/utils/RequiresLogin'
import * as  React from 'react'
import { RouteComponentProps } from 'react-router-dom'
import { Container, Header, Segment } from 'semantic-ui-react'
import { route } from 'utils/misc'

class NewAd extends React.PureComponent<IConnectProps> {
  public componentDidMount() {
    const { isFetching } = this.props
    if (!isFetching) {
      this.props.getUserInfo()
    }
  }
  public handleItemClick = (_e: any, { name }: any) => this.setState({ activeItem: name })
  public render() {
    const { auctionsLeft, hasPremium } = this.props
    return (
      <Container >
        <Meta
          title="Ny auksjon"
          description={'Auksjonsside for reptiler.'}
        />
        <DividerHeader as="h1">
          Ny Annonse {auctionsLeft >= 0 && `(${auctionsLeft})`}
        </DividerHeader>
        <RequiresLogin
          renderOnNotLoggedIn={(
            <Header inverted={true}>
              Denne siden krever innlogging.
                    </Header>
          )}
        >
          <Segment raised={true}>
            {auctionsLeft > 0 ? (
              <NewAdForm onSubmitSuccess={({ id }) => this.props.history.push(route.auction(id))} />
            ) : (
                <MessageNoAuctionsLeft auctionsLeft={auctionsLeft} hasPremium={hasPremium}/>
              )}
          </Segment >
        </RequiresLogin>
      </Container >
    )
  }
}

import { getUserInfo } from 'actions/user'
import { connect, Dispatch } from 'react-redux'
import { IStoreState } from 'store'
const connectCreator = connect(
  ({ forms, user }: IStoreState) => {
    const auctionsLeft = !!user.user ? user.user.auctions_left : -1
    return {
      valid: forms.auction.$form.valid,
      auctionsLeft,
      isFetching: user.isFetching,
      hasPremium: !!user.user && !!user.user.premium_package,
    }
  },
  (dispatch: Dispatch<{}>) => {
    return {
      getUserInfo: () => dispatch(getUserInfo()),
    }
  },
)

type IConnectProps = typeof connectCreator.allProps & RouteComponentProps<any>
export default connectCreator(NewAd)



