import FontAwesomeIcon from '@fortawesome/react-fontawesome'
import { Sex } from 'api/interfaces'
import BaseField from 'components/utils/BaseField'
import Icon from 'components/utils/Icon'
import ImageDescribe from 'components/utils/ImageDescribe'
import ImageUpload from 'components/utils/ImageUpload'
import { Norwegian } from 'flatpickr/dist/l10n/no.js'
import { AuctionFields } from 'forms/auction'
import { IAuctionForm } from 'forms/auction'
import * as React from 'react'
import Flatpickr from 'react-flatpickr'
import { actions, Errors, Form } from 'react-redux-form'
import {
  Button,
  ButtonProps,
  Checkbox,
  Divider,
  Dropdown,
  Form as UIForm,
  Header,
  Icon as UIIcon,
  Label,
  Responsive,
  Step,
  StepProps,
  TextArea,
} from 'semantic-ui-react'
import { sexString } from 'utils/txt'
import * as validators from 'utils/validators'
const debounce = require('lodash-es/debounce').default
const parse = require('date-fns/parse')
interface SexOptions {
  key: Sex
  text: string
  value: Sex
}
require('flatpickr/dist/themes/dark.css')

const sexOptions: SexOptions[] = [Sex.m, Sex.f, Sex.o].map(
  (value) => ({ key: value, value, text: sexString(value) }),
)

interface IProps {
  onSubmitSuccess: (ad: IAd) => any
}

const maxImages = 8

const titleCase = (str: string) => str.replace(/\b\S/g, (t) => t.toUpperCase())

const searcher = (strings: string[], query: string) => {
  const searchThese: string[] = strings.map((str) => str.toLowerCase())
  const queryLowered = query.toLowerCase()
  let bestIndex = -1
  for (const str of searchThese) {
    const index = str.indexOf(queryLowered)
    if (index !== -1 && (index < bestIndex || bestIndex === -1)) {
      bestIndex = index
    }
  }
  return bestIndex
}

enum Tab {
  images,
  reptile,
  auction,
}

interface IState {
  activeTab: Tab
}

interface ITabs extends StepProps {
  icon: any,
  header: string
  description: string
  key: Tab
}

class NewAdForm extends React.PureComponent<IConnectProps, IState> {
  public state = {
    activeTab: Tab.images,
  }
  public primaryUploaderOpener: any = null
  public debounced = debounce(
    (e: any) => {
      window.localStorage.setItem('auctionForm', JSON.stringify(e))
    },
    1000)

  public componentWillMount() {
    const auctionForm = window.localStorage.getItem('auctionForm')
    const { merge } = this.props
    if (auctionForm) {
      const jsoned = JSON.parse(auctionForm)
      merge(jsoned)
    }
  }
  public handleSubmit = (auction: any) => {
    const { images, enableBuyNow, enableAuction } = auction
    if (!images || !images.length) {
      return
    }
    const auctionKeys = ['askingPrice', 'minBidIncrease', 'interval']
    const localKeys = ['enableBuyNow', 'enableAuction']
    const buyNowKeys = ['buy_now_price']
    const auctionProps: any = Object.keys(auction)
      .filter(
        (key) => {
          if (localKeys.indexOf(key) !== -1) {
            return false
          }
          if (!enableAuction && auctionKeys.indexOf(key) !== -1) {
            return false
          }
          if (!enableBuyNow && buyNowKeys.indexOf(key) !== -1) {
            return false
          }
          return true
        },
    ).reduce(
      (r, key) => {
        r[key] = auction[key]
        return r
      },
      {},
    )
    this.props.submit({
      ...auctionProps,
      uploads: (images as IUploads[]).map(
        ({ public_id }) => ({
          public_id,
        }),
      ),
    }).then((a) => {
      const ad = ((a as any).ad as IAd)
      if (ad) {
        this.props.resetForms()
        this.props.onSubmitSuccess(ad)
      }
    })
  }
  public setRecommendedPrice = (categoryId: number) => {
    const { _mainCategories, _categories, merge } = this.props
    const category = _categories[categoryId]
    const maineCategory = _mainCategories[category.main_category_id]
    const recommendedPrice = category.recommended_price || maineCategory.recommended_price

    merge({
      askingPrice: recommendedPrice,
      buy_now_price: Math.floor(recommendedPrice * 1.5),
    })

  }
  public Images = () => {
    const { images } = this.props
    return (
      <>
        <ImageUpload
          openUploadWidgetFunc={(func) => { this.primaryUploaderOpener = func }}
          buttonProps={{
            color: 'blue',
            content: 'Last opp hovedbilde',
            fluid: true,
          }}
          widgetProps={{
            cropping: 'server',
            cropping_aspect_ratio: process.env.REACT_APP_CLOUDINARY_UPLOAD_PRESET_PRIMARY_CROPPING_ASPECT_RATIO,
            upload_preset: process.env.REACT_APP_CLOUDINARY_UPLOAD_PRESET_PRIMARY,
          }}
          onChange={(result) => {
            const img = images.slice(1)
            this.props.setPhotos([...result, ...img])
          }}
        />
        <ImageDescribe
          onPrimaryImageClick={() => this.primaryUploaderOpener()}
        />
        {(!!images.length && images.length <= maxImages) && (
          <ImageUpload
            buttonProps={{
              content: 'Last opp tillegsbilder',
            }}
            widgetProps={{
              max_files: maxImages - images.length,
            }}
            onChange={(result) => {
              const uploads = result.map(
                ({ public_id }) => ({ public_id }),
              )
              this.props.setPhotos([...images, ...uploads])
            }}
          />
        )}
      </>
    )
  }
  public Auction = () => {
    const { _categories, morph, enableBuyNow,
      enableAuction, _mainCategories, morphValue, askingPrice, selectedCategoryId } = this.props
    const selectedCategory = !!selectedCategoryId && _categories[Number(selectedCategoryId)]
    const selectedMainCategory = !!selectedCategory && _mainCategories[Number(selectedCategory.main_category_id)]
    const minPrice = selectedCategory && selectedCategory.min_price ||
      selectedMainCategory && selectedMainCategory.min_price || 0
    const recommendedPrice = selectedCategory && selectedCategory.recommended_price ||
      selectedMainCategory && selectedMainCategory.recommended_price || 0
    const morphs = Object.keys(morph)
      .map((k) => morph[k])
      .map(({ title }) => (title))

    if (morphs.indexOf(morphValue) === -1) {
      morphs.push(morphValue)
    }
    return (
      <>
        <BaseField
          label="Auksjon"
          typeComponent={Checkbox}
          model={AuctionFields.enableAuction}
        />
        <BaseField
          label="Kjøp nå"
          typeComponent={Checkbox}
          model={AuctionFields.enableBuyNow}
        />
        <Errors
          className="errors"
          model="auction"
          messages={{
            buyNowAuction: 'Du må velge minst en',
          }}
          component={({ children }) => (
            <Label basic={true} color="orange" pointing="above">{children}</Label>
          )}
        />
        {selectedCategory && (
          <p>
            Minsteprisen i den valgte kategorien <b>{selectedCategory.title}</b> er <b>kr. {minPrice}</b>.

            Den anbefalte minstebudet er er <b>kr. {recommendedPrice}</b>
          </p>
        )}
        <UIForm.Group widths="4">
          {enableAuction && (
            <>
              <BaseField
                label="Minstebud"
                type="number"
                model={AuctionFields.askingPrice}
                {...enableAuction && {
                  required: true,
                  min: minPrice,
                }}
              />
              <BaseField
                label="Minste budøkning"
                type="number"
                model={AuctionFields.minBidIncrease}
                min="1"
                required={enableAuction}
              />
              <BaseField
                label="Dager med auksjon"
                type="number"
                model={AuctionFields.interval}
                errors={{
                  ...validators.min(1, 'Minst 1 dag.'),
                  ...validators.max(14, 'Maksimalt 14 dager.'),
                }}
                required={enableAuction}
              />
            </>
          )}
          {enableBuyNow && (
            <BaseField
              label="Kjøp nå-pris"
              type="number"
              model={AuctionFields.buyNowPrice}
              errors={{
                ...validators.min(
                  Math.max(Number(askingPrice) + 1, minPrice),
                  enableAuction
                    ? 'Prisen må være høyere enn auksjonsprisen og minsteprisen'
                    : `Prisen må være høyere enn ${minPrice}`,
                ),
              }}
              required={enableBuyNow}
            />
          )}
        </UIForm.Group>
      </>
    )
  }

  public Reptile = () => {
    const { _categories, morph,
      _mainCategories, morphValue, date_of_birth } = this.props
    const categories = Object.keys(_categories)
      .map((k) => _categories[k])
      .map(({ id: key, title: text, main_category_id }) => {
        const main = _mainCategories[main_category_id]
        return {
          key,
          value: key,
          text,
          main,
          content: (
            <Header
              as="h4"
              size="tiny"
              content={text}
              subheader={main.title}
            />
          ),
        }
      },
    )
    const morphs = Object.keys(morph)
      .map((k) => morph[k])
      .map(({ title }) => (title))

    if (morphs.indexOf(morphValue) === -1) {
      morphs.push(morphValue)
    }
    return (
      <>
        <UIForm.Group widths="equal">
          <BaseField
            label="Reptil art"
            typeComponent={Dropdown}
            model={AuctionFields.category}
            required={true}
            controlProps={{
              options: categories,
              search:
                (options: typeof categories, query: string) => options.filter(
                  ({ main, text }) => searcher([main.title, text], query) !== -1,
                ).sort(
                  ({ main, text }, { main: mainB, text: textB }) => {
                    const bestIndexA = searcher([main.title, text], query)
                    const bestIndexB = searcher([mainB.title, textB], query)
                    return bestIndexA - bestIndexB
                  },
                ),
              placeholder: 'Reptil art',
              noResultsMessage: 'Ingen resultat',
              selection: true,
              onChange: (_e: any, d: any) => {
                this.props.setCategory(d.value)
                this.setRecommendedPrice(d.value as number)
              },
            }}
          />
          <BaseField
            label="Morph/lokasjon"
            typeComponent={Dropdown}
            model={AuctionFields.morph}
            required={true}
            controlProps={{
              options: morphs.map((m) => ({ key: m, text: m, value: m })),
              search: true,
              allowAdditions: true,
              additionPosition: 'bottom',
              additionLabel: 'Oprette: ',
              placeholder: 'Morph',
              noResultsMessage: 'Ingen resultat',
              selection: true,
              onAddItem: (_e: any, d: any) => this.props.setMorph(titleCase(d.value)),
              onChange: (_e: any, d: any) => this.props.setMorph(d.value),
            }}
          />
          <BaseField
            label="Kjønn"
            typeComponent={Dropdown}
            model={AuctionFields.sex}
            required={true}
            controlProps={{
              options: sexOptions,
              selection: true,
              onChange: (_e: any, d: any) => this.props.setSex(d.value),
            }}
          />
        </UIForm.Group>
        <UIForm.Group widths="equal">
          <UIForm.Field>
            <label>Født</label>
            <Flatpickr
              value={date_of_birth as string}
              options={{
                altInput: true,
                altFormat: 'j. F Y',
                dateFormat: 'Y-m-d',
                locale: Norwegian,
              }}
              onChange={(_s: Date[], dateStr: string) => {
                if (date_of_birth !== dateStr) {
                  this.props.setField(
                    AuctionFields.date_of_birth,
                    dateStr,
                  )
                }
              }}
            />
            <Errors
              model={AuctionFields.date_of_birth}
              show={(field) => !field.pristine}
              messages={{
                bagh: 'Datoen kan ikke være tidligere enn i dag',
              }}
              component={({ children }) => (
                <Label basic={true} color="orange" pointing="above">{children}</Label>
              )}
            />
          </UIForm.Field>
          <BaseField
            label="Vekt (g)"
            type="number"
            model={AuctionFields.weight}
            min="0"
          />
        </UIForm.Group>
        <BaseField
          label="Beskrivelse av reptil"
          // tslint:disable-next-line
          placeholder="Beskriv din reptil her. En god beskrivelse gjør det enklere for kjøper å få oversikt, og dermed velge akkurat din reptil."
          typeComponent={TextArea}
          model={AuctionFields.description}
          required={true}
        />
        <BaseField
          label="Reptilens helse"
          placeholder="Om du kjenner til noen helsemessige utfordringer, er du pålagt å opplyse om dem."
          typeComponent={TextArea}
          model={AuctionFields.misconceptions}
        />
        <BaseField
          label="Diett"
          placeholder="Type fôr."
          model={AuctionFields.diet}
        />
        <BaseField
          label="Avlsklar"
          typeComponent={Checkbox}
          model={AuctionFields.ready_for_breeding}
        />
        <BaseField
          label="Bevist avler"
          typeComponent={Checkbox}
          model={AuctionFields.proven_breeder}
        />
        <BaseField
          label="Genetikk bombe"
          typeComponent={Checkbox}
          model={AuctionFields.genetic_bomb}
        />
      </>
    )
  }
  public Hider = (
    { active, children, next, nextDisabled, buttonProps }: {
      active: boolean, children: any, next?: boolean, nextDisabled?: boolean, buttonProps?: ButtonProps,
    }) => {
    const { activeTab } = this.state
    return (
      <div
        style={{ display: active ? 'block' : 'none' }}
      >
        {children}
        <div style={{ display: 'flex', justifyContent: 'space-between' }}>
          <div>
            {activeTab !== 0 && (
              <Button
                size="big"
                icon={true}
                content={<><Icon icon="chevron-double-left" /> Forrige</>}
                onClick={(e) => {
                  e.preventDefault()
                  e.stopPropagation()
                  this.setState({ activeTab: activeTab - 1 })
                }}
              />
            )}
          </div>
          {next && (
            <Button
              size="big"
              primary={true}
              icon={true}
              disabled={nextDisabled}
              content={<>Neste <Icon icon="chevron-double-right" /></>}
              onClick={(e) => {
                e.preventDefault()
                e.stopPropagation()
                this.setState({ activeTab: activeTab + 1 })
              }}
              {...buttonProps}
            />
          )}
        </div>
      </div>)
  }

  public tabs = (): ITabs[] => {
    const { imagesValid, reptileValid, auctionValid } = this.props
    return [
      {
        icon: 'images',
        key: Tab.images,
        header: '1. Bilder',
        description: 'Last opp bilder',
        success: imagesValid,
        color: 'blue',
      },
      {
        icon: 'bug',
        key: Tab.reptile,
        header: '2. Reptil',
        description: 'Info om reptilen',
        disabled: !imagesValid,
        success: reptileValid,
      },
      {
        icon: 'gavel',
        key: Tab.auction,
        header: '3. Auksjon',
        description: 'Pris og type',
        disabled: !reptileValid,
        success: auctionValid,
      },
    ]
  }

  public Steps = (props: StepProps) => {
    const { activeTab } = this.state
    return (
      <Step.Group fluid={true} unstackable={true} {...props}>
        {this.tabs().map(
          ({ success, icon, header, description, key, ...rest }) => (
            <Step
              key={key}
              onClick={() => this.setState({ activeTab: key })}
              active={activeTab === key}
              {...rest}
            >
              <UIIcon>
                <span className="fa-layers fa-fw">
                  < FontAwesomeIcon icon={icon} />
                  {success && (
                    <>
                      <FontAwesomeIcon icon="check" transform="right-6 down-6" color="#72b950" />
                    </>
                  )}
                </span>
              </UIIcon>
              <Step.Content>
                <Step.Title>{header}</Step.Title>
                <Step.Description>{description}</Step.Description>
              </Step.Content>
            </Step>
          ),
        )}
      </Step.Group>
    )
  }

  public render() {
    const { Images, Auction, Reptile, Hider, Steps } = this
    const { activeTab } = this.state
    const { imagesValid, reptileValid, auctionValid, auctionsLeft, isFetching } = this.props
    return (
      <Form
        className="ui form"
        model="auction"
        onSubmit={this.handleSubmit}
        hideNativeErrors={true}
        onChange={this.debounced}
        validators={{
          '': {
            // Form-level validator
            buyNowAuction: (vals: IAuctionForm) => vals.enableBuyNow || vals.enableAuction,
          },
          'date_of_birth': {
            bagh: (val) => {
              return val && parse(val) < new Date()
            },
          },
        }}
      >
        <Responsive minWidth={Responsive.onlyTablet.minWidth}>
          <Steps />
          <Divider hidden={true} />
        </Responsive>
        <Hider
          active={activeTab === Tab.images}
          next={imagesValid}
        >
          <Header>Bilder</Header>
          <Images />
        </Hider>
        <Hider
          active={activeTab === Tab.reptile}
          next={reptileValid}
        >
          <Header>Reptil</Header>
          <Reptile />

        </Hider>
        <Hider
          active={activeTab === Tab.auction}
          next={true}
          buttonProps={{
            positive: reptileValid && auctionValid && imagesValid,
            disabled: auctionsLeft <= 0 || isFetching,
            type: 'submit',
            primary: false,
            content: <>Publiser annonse <Icon icon="chevron-double-right" /></>,
            onClick: () => null,
          }}
        >
          <Header>Auksjon</Header>
          <Auction />
        </Hider>
      </Form >

    )
  }
}

interface IUploads {
  public_id: string
}

const checkValid = (form: any, keys: AuctionFields[], strip: string) => {
  for (const key of keys) {
    const stripped = key.slice(strip.length)
    if (!form[stripped].valid) {
      return false
    }
  }
  return true
}
import { newAd } from 'actions/ad'
import { IAd, IAdNew } from 'api/interfaces'
import { connect, Dispatch } from 'react-redux'
import { IStoreState } from 'store'
const connectCreator = connect(
  ({ base, forms, auction, user, ads }: IStoreState) => {
    const auctionsLeft = user.user ? user.user.auctions_left : -1
    const imagesValid = auction.images && !!auction.images.length
    const reptileValid = checkValid(
      forms.auction, [
        AuctionFields.category,
        AuctionFields.morph,
        AuctionFields.sex,
        AuctionFields.date_of_birth,
        AuctionFields.diet,
        AuctionFields.weight,
        AuctionFields.description,
        AuctionFields.misconceptions,
        AuctionFields.diet,
        AuctionFields.ready_for_breeding,
        AuctionFields.proven_breeder,
      ],
      'auction.')
    const auctionValid = checkValid(
      forms.auction, [
        AuctionFields.enableBuyNow,
        AuctionFields.enableAuction,
        AuctionFields.minBidIncrease,
        AuctionFields.agreedTOS,
        AuctionFields.askingPrice,
        AuctionFields.interval,
        AuctionFields.buyNowPrice,
      ],
      'auction.')
    return {
      date_of_birth: auction.date_of_birth,
      auctionsLeft,
      enableAuction: auction.enableAuction,
      imagesValid,
      reptileValid,
      auctionValid,
      isFetching: ads.isFetching,
      enableBuyNow: auction.enableBuyNow,
      askingPrice: auction.askingPrice,
      buy_now_price: auction.buy_now_price,
      images: auction.images,
      selectedCategoryId: auction.category,
      valid: forms.auction.$form.valid && auctionsLeft > 0,
      morph: base.morphs,
      morphValue: auction.morph,
      _categories: base.categories,
      _mainCategories: base.mainCategories,
    }
  },
  (dispatch: Dispatch<{}>) => {
    return {
      submit: (ad: IAdNew) =>
        dispatch(newAd(ad)),
      setField: (field: AuctionFields, value: any) => dispatch(actions.change(field, value)),
      setPhotos: (value: IUploads[]) =>
        dispatch(actions.change(AuctionFields.images, value)),
      mergePhotos: (values: IUploads[]) =>
        dispatch(actions.merge(AuctionFields.images, values)),
      setSex: (value: Sex) =>
        dispatch(actions.change(AuctionFields.sex, value)),
      setCategory: (value: number) =>
        dispatch(actions.change(AuctionFields.category, value)),
      setMorph: (value: string) =>
        dispatch(actions.change(AuctionFields.morph, value)),
      merge: (form: any) =>
        dispatch(actions.merge('auction', form)),
      resetForms: () => {
        dispatch(actions.reset('auction'))
        dispatch(actions.reset('images'))
      },
    }
  },
)

type IConnectProps = typeof connectCreator.allProps & IProps
export default connectCreator(NewAdForm)



