import * as React from 'react'
import { Link } from 'react-router-dom'
import { Message } from 'semantic-ui-react'
import { route } from 'utils/misc'

export default ({ auctionsLeft, hasPremium }: { auctionsLeft: number, hasPremium: boolean }) => {
  if (auctionsLeft > 0) {
    return null
  }
  return (
    <Message
      negative={true}
    >
      <Message.Header>
        {hasPremium ? 'Du har ikke flere anonnser igjen denne måneden' : 'Kun for aktive abbonenter'}
        </Message.Header>
      <p>
          {hasPremium
            ? 'Med et aktivt abbonomant vil du få nye anonnser hver måned, eller du kan kjøpe flere anonnser.'
          : 'Det gis flere fordeler til medlemsskap.'
        }
      </p>
        <p>
          Du kan administrere ditt abbonomant på <Link to={route.member()}>medlemssiden</Link>
        </p>
    </Message>
      )
    }



